<template>
	<div class="loginFormDiv">
		<span class="helper"></span>
		<div id="loginForm">
			<input
				v-model="email"
				class="login_input"
				type="text"
				placeholder=""
				@focus="emailCheck = false"
			/>
			<span v-if="emailCheck" class="errorMsg">{{ emailMsg }}</span>
			<input
				v-model="password"
				class="login_input"
				type="password"
				placeholder=""
				@focus="pwCheck = false"
			/>
			<span v-if="pwCheck" class="errorMsg">{{ pwMsg }}</span>
			<button id="login_btn" @click="login()" @keyup.enter="login()"></button>
			<div id="login_bottom">
				<hr id="div_line" />
				<span class="login_bottom_text" @click="findPW"> </span>
				<img id="text_div" src="../assets/icons/text_div.png" />
				<span class="login_bottom_text" @click="signUp"></span>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: "Login",
	data() {
		return {
			email: "",
			password: "",
			show: false,
			emailMsg: " !",
			pwMsg: " !",
			emailCheck: false,
			pwCheck: false,
		};
	},
	mounted() {},

	methods: {
		emailValidation(email) {
			let regExp = /^([\w-]+(?:\.[\w-]+)*)@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$/i;
			if (regExp.test(email)) return true;
			else return false;
		},
		login() {
			if (!this.emailValidation(this.email)) {
				this.emailCheck = true;
				return;
			}

			if (this.password == "") {
				this.pwCheck = true;
				return;
			}

			this.$store
				.dispatch("user/login", {
					email: this.email,
					password: this.password,
				})
				.then(response => {
					console.log(response);
				})
				.catch(error => {
					console.log(error);
				});
		},
		signUp() {
			console.log("");
			this.$router.push("/signUp");
		},
		findPW() {},
	},
};
</script>

<style>
.loginFormDiv {
	height: 100%;
	width: 100%;
}

.helper {
	display: inline-block;
	height: 100%;
	vertical-align: middle;
}

#loginForm {
	display: inline-block;
	vertical-align: middle;
	margin: 0 auto;
	width: 400px;
}

.login_input {
	width: 100%;
	height: 50px;
	border: 1px solid silver;
	padding: 10px;
	margin-top: 20px;
}

.login_input:focus {
	outline: 2px solid rgb(250, 223, 153);
}

#login_btn {
	margin-top: 20px;
	width: 100%;
	height: 65px;
	background-color: rgb(250, 223, 153);
	color: white;
	border-radius: 0px;
	font-size: 20px;
	outline: none;
}

#login_btn:hover {
	opacity: 0.8;
}

#div_line {
	border: solid 0.5px rgb(230, 229, 229);
	margin-bottom: 5px;
}

#login_bottom {
	width: 100%;
	margin-top: 20px;
	text-align: center;
}

#text_div {
	height: 13px;
	width: 1px;
	margin: 0 5px 0 5px;
}

.login_bottom_text {
	font-size: 13px;
	color: rgb(66, 66, 66);
	font-weight: 300;
	cursor: pointer;
}

.login_bottom_text:hover {
	color: rgb(0, 0, 0);
}

.errorMsg {
	color: rgb(250, 223, 153);
	font-size: 12px;
	margin-left: 5px;
}

@media screen and (max-width: 500px) {
	#loginForm {
		width: 250px;
	}
}
</style>